import React, { Component } from 'react';
import $ from "jquery";
import banner from './banner.jpg';
import map from './map.jpg';
import PaymentInfo from '../Reused/PaymentInfo';
import LINE from '../Reused/LINE';
import "./News.css";

class News extends Component{
    constructor(props){
        super(props);
        this.state = {
            newsInfo: {}
        }
    }
    componentDidMount(){
        this.getNewsData();
    }

    getNewsData(){
        $.ajax({
          url: process.env.PUBLIC_URL + "/newsData.json",
          dataType: "json",
          cache: false,
          success: function(newsInfo) {
            this.setState({ newsInfo });
          }.bind(this),
          error: (xhr, status, err) => {
            console.log(err);
            alert(err);
          }
        });
    }

    render(){
        const { newsInfo } = this.state;
        const announces = newsInfo.announces ? (
          <ul className="announces">
            {newsInfo.announces.map( (an, i) =>
            <li key={"an" + i}>{an}</li>)}
          </ul>
        ) : null;
        return (
          <div className="news">
            <img src={banner} alt="logo"/>
            <h1>{newsInfo.hours}</h1>
            <h1>{announces}</h1>
            <br/>
            <br/>
            <LINE />
            <h1>201571 :</h1>
            <ul className="prices">
                <li>BG-65 <p>290</p></li>
                <li>BG-65Ti <p>320</p></li>
                <li>BG-68Ti/70 <p>330</p> (BG70)</li>
                <li>BG-66 <p>340</p></li>
                <li>BG-66U <p>350</p></li>
                <li>BG-66F/80/85<p>360</p> (BG85)</li>
                <li>NBG-80P/95/98/99 <p>370</p></li>
                <li>BG-AS <p>370</p></li>
                <li>BG-AB <p>380</p></li>
                <li><p>200</p>/<p>250</p> </li>
                <li><p>150</p><p>200</p> </li>
                <li>, <p>50</p></li>
            </ul>
            <h1>2014-03-01   :
            207 ()
            </h1>
            <img src={map} alt="map"/>
            <ul className="h1">
                <li></li>
                <li></li>
                <li></li>
            </ul>
            <PaymentInfo/>
          </div>
        );
    }
};

export default News;