import React from 'react';
import { Link } from 'react-router-dom';
import main from './main.gif';
import './Navbar.css';

const navBar = () => {
    return (
      <header className="navbar">
            <div className="flex">
                <Link to="/"><img src={main} alt="main" /></Link>
                <div>
                    <h1> MASUKA (04)2243-0949</h1>
                    <h2>()</h2>
                    <div>
                        <Link to="/news"></Link>
                    </div>
                    <div>
                        <Link to="/questions"></Link>
                    </div>
                    <div>
                        <Link to="/about"></Link>
                    </div>
                    <div>
                        <Link to="/paymentinfo"></Link>
                    </div>
                </div>
          </div>
            <h3>, </h3>
      </header>
    );
};

export default navBar;
